.header {
  width: 100%;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 12vh;
  padding: 1.5rem 3rem;
}

.header_left {
  display: flex;
  color: white;
  justify-content: flex-start;
  align-items: center;
  width: 33vw;
}

.header_left_section {
  font-size: 15px;
  letter-spacing: 0.08rem;
  font-weight: bold;
  display: flex;
  line-height: 26px;
  margin-left: 5rem;
}

.header_left_section_items {
  padding: 0 1rem;
}

.header_left_bnb {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  letter-spacing: 0.08rem;
  font-weight: bold;
}

.header_right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: white;
  font-size: 15px;
  letter-spacing: 0.08rem;
  font-weight: bold;
  min-width: 25vw;
}

.header_right_search_container {
  display: flex;
  align-items: center;
  border-radius: 0.5rem;
  border: 1px solid white;
  width: 20vw;
  margin-right: 3rem;
  padding: 0.5rem 1rem 0.5rem 1rem;
}

.header_right_search_searchText {
  width: 90%;
  background-color: black;
  font-size: 15px;
  border: none;
  color: white;
}

.header_right_search_searchText:focus {
  outline:  none;
}

.header_right_search_searchText::placeholder {
  color: white;
}

.header_right_search_searchIcon {
  display: flex;
  flex: 10%;
  justify-content: center;
  cursor: pointer;
}

.header_right_items-3 {
  text-decoration: underline;
}

.header_right_items-1 {
  margin-right: 3rem;
  cursor: pointer;
}

.header_right_items-4 {
  margin-left: 3rem;
}

.header_right_items-2,.header_right_items-3 {
  margin: 0 0.7rem;
}

.search-icon, .lock-icon, .menu-icon {
  color: white;
}

.menu-icon {
  width: 30px;
  height: 50px;
  cursor: pointer;
}

.search-icon, .lock-icon {
  width: 20px;
  height: 40px;
}

@media screen and (max-width: 1100px) {
  .header_left_section {
    display: none;
  }
  .header_right_items-3,.header_right_items-2,.header_right_items-1 {
    display: none;
  }
  .header_right_search_container {
    display: none;
  }
}

@media screen and (max-width: 1100px) {
  .header {
    padding: 2rem 2rem;
  }
}